import { Title } from '@angular/platform-browser';
import { SharedStorage } from './../../../core/shared/storageShared';
import { ProductsService } from './../../../core/shared/services/products.service';
import { SeoService } from './../../../core/shared/services/seo.service';
import { AddProductsExcelComponent } from './../../../core/components/modales/add-products-excel/add-products-excel.component';
import { AlaunnaProductsComponent } from './../../../core/components/modales/alaunna-products/alaunna-products.component';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SnotifyService } from 'ng-snotify';
import { BsModalService } from 'ngx-bootstrap/modal';
import swal from 'sweetalert2';
import { MarketProductComponent } from '../../../core/components/modales/market-product/market-product.component';

@Component({
  selector: 'app-create-product',
  templateUrl: './create-product.component.html',
  styleUrls: ['./create-product.component.css']
})
export class CreateProductComponent implements OnInit {



  ProductData = {} as any;
  currentMarketData = {} as any;

  categories;
  filterdsubCat;
  fitlerdSubsubCat;
  subsubcategories;
  inputs = [] as any;

  currentImageId = 0;
  CurrentCategory = {} as any;
  constructor(private modalService: BsModalService,
    private router: Router,
    private productController: ProductsService,
    private snotify: SnotifyService, private seoService: SeoService  , private title : Title) {
      this.title.setTitle('الاأونا - اضافه منتج جديد');
      this.seoService.createLinkForCanonicalURL();
  }

  ngOnInit() {
    this.currentMarketData = SharedStorage.current_user_data['markets'][0];
    if (!this.currentMarketData) {
      this.snotify.warning('لا يمكن الوصول لهذه الصفحه الا اذا كان لديك متجر');
      this.router.navigate(['/auth/seller-singup']);
      return;
    }
    this.loadAllProductCategory();
    this.loadAllProductSubCategory();
    this.loadAllProductSubSubCategory();
  }
  onShowProductModel() {
    const modelRef = this.modalService.show(AlaunnaProductsComponent, { class: ' ProductModel ' });
  }
  onAddFromExcelSheetClicked() {
    const modelRef = this.modalService.show(AddProductsExcelComponent, { class: ' ProductModel ' });
  }
  updateInputs() {
    let curentSubcategory;
    if (this.filterdsubCat) {
      curentSubcategory = this.filterdsubCat.filter(x => {
        // tslint:disable-next-line:triple-equals
        return x.id == this.ProductData['subCategory_id'];
      })[0];
    }
    let inputs = [];
    if (this.CurrentCategory && this.CurrentCategory.inputs) {
      inputs = inputs.concat(JSON.parse(this.CurrentCategory.inputs));
    }
    if (curentSubcategory && curentSubcategory.inputs) {
      inputs = inputs.concat(JSON.parse(curentSubcategory.inputs));
    }
    // // console.log(curentSubcategory);
    this.inputs = inputs;
  }
  loadAllProductCategory() {
    this.productController.getCAtegoryById(this.currentMarketData['marketcategory_id']).subscribe(data => {
      if (data['status'] === 'success' && data['data']) {
        this.CurrentCategory = data['data'];
        // // console.log(this.CurrentCategory);
        this.ProductData['category_id'] = this.CurrentCategory['id'];
        this.updateInputs();
        // SharedStorage.product_categories = data['data']['rows'];
      } else {
        this.snotify.error('تم مسح التصنيف الذي تنتمي اليه عند تحديث البيانات برجاء تعديل بيانات المتجر الخاص بك مع التصنيف الجديد');
        this.router.navigate(['user/profile']);
      }
    },
      err => {
        this.snotify.error('هناك مشكله برجاء التحقق من الاتصال بالانترنت واعاده الاتصال');
      }
    );
  }
  onSubCategoryChange() {
    // // console.log('changes');
    // // console.log(this.subsubcategories);
    this.fitlerdSubsubCat = this.subsubcategories.filter(x => {
      return x.subCategory_id == this.ProductData['subCategory_id'];
    });
    this.updateInputs();
  }
  loadAllProductSubCategory() {
    this.productController.getAllProductSubCategory(false).subscribe(
      data => {
        if (data['status'] === 'success') {
          this.filterdsubCat = data['data']['rows'].filter(x => {
            return x.productcategory_id === this.currentMarketData['marketcategory_id'];
          });
          SharedStorage.product_subcategories = data['data']['rows'];
        } else {
          this.snotify.error('هناك مشكله برجاء التحقق من الاتصال بالانترنت واعاده الاتصال');
        }
      },
      err => {
        this.snotify.error('هناك مشكله برجاء التحقق من الاتصال بالانترنت واعاده الاتصال');
      }
    );
  }
  loadAllProductSubSubCategory() {
    this.productController.getAllProductSubSubCategory(false).subscribe(data => {
      if (data['status'] === 'success') {
        this.subsubcategories = data['data']['rows'];
        this.fitlerdSubsubCat = data['data']['rows'];
        SharedStorage.product_subsubcategories = data['data']['rows'];
      } else {
        this.snotify.error('هناك مشكله برجاء التحقق من الاتصال بالانترنت واعاده الاتصال');
      }
    },
      err => {
        this.snotify.error('هناك مشكله برجاء التحقق من الاتصال بالانترنت واعاده الاتصال');
      }
    );
  }
  ongetNewImage(image_id) {
    if (!this.ProductData['images']) {
      this.ProductData['images'] = [];
    }

    document.getElementById('file').click();
  }
  onEditImageClicked(image_id) {
    if ((image_id && image_id !== -1) || image_id === 0) {
      this.currentImageId = image_id;
    }
    document.getElementById('Editfile').click();
  }
  onFileChanges(evt) {
    const files = evt.target.files;
    for (let i = 0; i < files.length; i++) {
      const file = files[i];
      if (files && file) {
        const reader: FileReader = new FileReader();
        reader.onload = x => {
          const base64 = reader['result'] as string;
          if (!this.ProductData.images) {
            this.ProductData['images'] = [];
          }
          this.ProductData.images.push({
            base64: base64.split(',')[1],
            alt: 'alaunna image',
            description: 'alaunna image',
          });
        };
        reader.readAsDataURL(file);
      }
    }
  }
  onEditFileChange(evt) {
    const files = evt.target.files;
    const file = files[0];
    if (files && file) {
      const reader: FileReader = new FileReader();
      reader.onload = x => {
        const base64 = reader['result'] as string;
        this.ProductData['images'][this.currentImageId] = {
          base64: base64.split(',')[1],
          alt: 'alaunna',
          description: 'alaunna'
        }
      };
      reader.readAsDataURL(file);
    }
  }
  onAddNewClicked() {
    if (!this.ProductData.images || this.ProductData.images.length === 0) {
      this.snotify.warning('برجاء قم باضافه صوره علي الاقل للمنتج');
      return;
    }
    if (!this.ProductData.name || !this.ProductData.description) {
      this.snotify.warning('برجا كتابه اسم المنتج والوصف الخاص بالمنتج');
      return;
    }
    if (!this.ProductData.national_qr){
      this.snotify.warning('برجاء كتبه الكود الدولي الخاص بالمنتج ');
      return;
    }
    if (this.ImagePreProccessing()) {
      swal('خطأ', 'برجاء قم برفع صور ذات جوده جيده علي ان تكون ابعاد الصوره 700 X 700', 'error');
      return;
    }
    this.ProductData['product_type'] = 'new_product';
    this.ProductData['extra_input'] = this.rebuildingInputs();
    if (!this.ProductData['extra_input']) {
      swal('خطأ', 'برجاء اختيار كل المدخلات الاضافيه لمساعده المستخدم علي اختيار بعض الطلبات المخصصه', 'warning');
      return;
    }
    this.ProductData['sub_for'] = 'createTheRef';
    this.ProductData['market_id'] = this.currentMarketData['id'];
    this.productController.createNewProduct(this.ProductData).subscribe(data => {
      // // console.log(data);
      if (data['status'] === 'success') {
        this.snotify.success('تم اضافه المنتج بنجاح');
        this.router.navigate(['/markets/details/' + this.currentMarketData['id'] + '_' + this.currentMarketData.name]);
      } else {
        this.snotify.warning('حصل مشكله عند اضافه المنتج برجاء التأكد من البيانات وادخالها بصوره صحيحه');
      }

    }, err => {
      this.snotify.warning('هناك مشكله برجاء التحقق من الاتصال بالانترنت واعاده الاتصال');
    });
  }
  ImagePreProccessing() {
    let HaveError = false;
    for (let i = 0; i < this.ProductData.images.length; i++) {
      let ImageEl = document.getElementById(i + 'image');
      ImageEl.classList.remove('error');
      var realSize = this.realImgDimension(ImageEl);
      if (realSize.naturalHeight != 700 || realSize.naturalWidth != 700) {
        ImageEl.classList.add('error');
        HaveError = true;
      }
    }
    return HaveError;
  }
  realImgDimension(img) {
    var i = new Image();
    i.src = img.src;
    return {
      naturalWidth: i.width,
      naturalHeight: i.height
    };
  }
  onCopyDatafromPreviousProduct() {
    const modelRef = this.modalService.show(MarketProductComponent, { class: ' ProductModel ' });
    this.modalService.onHide.subscribe(data => {
      let SelectedProducts = SharedStorage.WholeSelectedProducts[0];
      // // console.log(SelectedProducts);
      this.ProductData = {
        name: SelectedProducts.name,
        national_qr: SelectedProducts.nationalQr,
        description: SelectedProducts.description,
        size: SelectedProducts.size,
        size_type: SelectedProducts.sizeType,
        extra_input: SelectedProducts.extraInput,
        category_id: SelectedProducts.category_id,
        subCategory_id: SelectedProducts.subCategory_id,
        subSubCategory_id: SelectedProducts.subSubCategory_id,
        inner_code: SelectedProducts['market'][0].Market_Products['InnerCode'],
        selling_price: SelectedProducts['market'][0].Market_Products['Selling_price'],
        purchasing_price: SelectedProducts['market'][0].Market_Products['Purchasing_price'],
        awshn_display: SelectedProducts['market'][0].Market_Products['Showen'],
        expire_date: SelectedProducts['market'][0].Market_Products['Expire_date'],
        tax: SelectedProducts['market'][0].Market_Products['Tax'],
        quantity: SelectedProducts['market'][0].Market_Products['quantity'],
      }
      this.inputs = JSON.parse(SelectedProducts.extraInput);
      this.CurrentCategory = SelectedProducts.category;
      this.onSubCategoryChange();
    })
  }
  rebuildingInputs() {
    let newInput = [];
    for (let i = 0; i < this.inputs.length; i++) {
      if (!this.inputs[i].value) {
        return false;
      }
      newInput.push({ name: this.inputs[i].name, value: this.inputs[i].value });
    }
    return JSON.stringify(newInput);
  }
}
