import { ProductsService } from './../../../shared/services/products.service';
import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';


@Component({
  selector: 'app-product-rating-data',
  templateUrl: './product-rating-data.component.html',
  styleUrls: ['./product-rating-data.component.css']
})
export class ProductRatingDataComponent implements OnInit {

  ProductData = {} as any;

  ProductRatings = [] as any;
  offset = 0;
  constructor(public bsModalRef: BsModalRef , private productsController : ProductsService) { }

  ngOnInit() {
    this.getAllProductRatingData(this.ProductData['id']);
  }
  getAllProductRatingData(product_id){
    this.productsController.getallRates(this.ProductData.id ,false, true ,this.offset  ).subscribe(data => {
      if (data['status'] == 'success'){
        if (this.offset == 0){
          this.ProductRatings = data['data']['rows'];
        } else {
          this.ProductRatings = this.ProductRatings.concat(data['data']['rows']);
        }
      }
    } ,err => {
      alert('حدثت مشكله')
    })
  }
  onScroll(){
    this.offset++;
    this.getAllProductRatingData(this.ProductData['id']);
    // console.log('scrolled');
  }

}
