import { SharedStorage } from './../../../shared/storageShared';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Component, OnInit } from '@angular/core';
import swal from 'sweetalert2';

@Component({
  selector: 'app-select-ptoduct-options',
  templateUrl: './select-ptoduct-options.component.html',
  styleUrls: ['./select-ptoduct-options.component.css']
})
export class SelectPtoductOptionsComponent implements OnInit {
  ProductName = '';
  options = {} as any;
  array = Array;
  constructor(public bsModalRef: BsModalRef) { }

  ngOnInit() {
    // console.log(this.options);
  }
  onSaveClicked() {
    this.options.forEach(element => {
      // console.log(element);
      if (!element.user_value) {
        swal('خطأ', 'برجاء قم باختيار كل الخصائص حتي يتم توصيل المنتج بشكل صحيح', 'error');
        return;
      }
    });
    for (let i = 0; i < this.options.length; i++) {
      if (!this.options[i]['user_value']) {
        swal('خطأ', 'برجاء قم باختيار كل الخصائص حتي يتم توصيل المنتج بشكل صحيح', 'error');
        break;
      }
      if (i == this.options.length - 1) {
        SharedStorage.curent_option = this.options;
        this.bsModalRef.hide();
      }
    }

  }

}
