import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { sharedClass } from '../sharedattrubite';
import { TransferHttpService } from '@gorniv/ngx-transfer-http';

@Injectable({
  providedIn: 'root'
})
export class LocationService {
  Cities;
  Countries;
  constructor(public http: TransferHttpService) {
    // console.log('Hello LocationProvider Provider');
  }
  getAllCountry(offset) {
    if (offset != false || offset == 0) {
      var URL = `${sharedClass.BASE_URL}/location/countries?offset=${offset}`;
    } else {
      var URL = `${sharedClass.BASE_URL}/location/countries`;
    }
    return this.http.get(URL);
  }
  getAllcities(offset) {
    if (offset != false || offset == 0) {
      var URL = `${sharedClass.BASE_URL}/location/cities?offset=${offset}`;
    } else {
      var URL = `${sharedClass.BASE_URL}/location/cities?offset`;
    }
    return this.http.get(URL);
  }
  addNewCity(CityData){
    let URL = `${sharedClass.BASE_URL}/location/city/create`;
    return this.http.post(URL , CityData);
  }
  addNewCountry(CountryData){
    let URL = `${sharedClass.BASE_URL}/location/country/create`;
    return this.http.post(URL , CountryData);
  }

  deleteCountry(country_id){
    let URL = `${sharedClass.BASE_URL}/location/country/remove`;
    return this.http.post(URL , {id : country_id});
  }
  deleteCity(city_id){
    let URL = `${sharedClass.BASE_URL}/location/city/remove`;
    return this.http.post(URL  ,{id : city_id})
  }
  filtercitiesAccourdingToCountryID(countryID) {
    let URL = `${sharedClass.BASE_URL}/location/filtercities/${countryID}`;
    return this.http.get(URL);
  }

}
