import { Title } from '@angular/platform-browser';
import { ProductsService } from './../../../core/shared/services/products.service';
import { SharedStorage } from './../../../core/shared/storageShared';
import { sharedClass } from './../../../core/shared/sharedattrubite';
import { SeoService } from './../../../core/shared/services/seo.service';
import { SnotifyService } from 'ng-snotify';
import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

import swal from 'sweetalert2';

@Component({
  selector: 'app-edit-product',
  templateUrl: './edit-product.component.html',
  styleUrls: ['./edit-product.component.css']
})
export class EditProductComponent implements OnInit {

  ProductData = { market: [{ Market_Products: {} }] } as any;
  currentMarketData = SharedStorage.current_user_data.markets[0];

  categories;
  filterdsubCat;
  fitlerdSubsubCat;
  subsubcategories;
  inputs = [] as any;
  ImageBase = sharedClass.IMAGE_BASE_URL;
  currentImageId = 0;
  constructor(private route: ActivatedRoute,
    private productsController: ProductsService,
    private snotify: SnotifyService, private router: Router,private seoService : SeoService  , private title : Title ) {

      this.seoService.createLinkForCanonicalURL();
    this.loadAllProductSubCategory();
    this.loadAllProductSubSubCategory();
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      if (params['id']) {
        this.productsController.getProductData(params['id']).subscribe(data => {
          // console.log(data);
          if (data['status'] == 'success') {
            if (data['data']['owner'] != SharedStorage.current_user_data['markets'][0].id) {
              this.router.navigate(['/home']);
              swal('خطأ', 'لايمكن تعديل منتج لا تمتلكه', 'error');
              return;
            }
            this.ProductData = data['data'];
            this.title.setTitle(this.ProductData.name);
            this.onSubCategoryChange();
            if (this.ProductData.extraInput) {
              this.inputs = JSON.parse(this.ProductData.extraInput);
              // console.log(this.inputs)
            }
          }
        }, err => {
          // console.log(err);
        })
      }
    })
  }
  ongetNewImage(image_id) {
    if (!this.ProductData['images']) {
      this.ProductData['images'] = [];
    }

    document.getElementById('file').click();
  }
  onEditImageClicked(image_id) {
    if ((image_id && image_id !== -1) || image_id === 0) {
      this.currentImageId = image_id;
    }
    document.getElementById('Editfile').click();
  }
  onFileChanges(evt) {
    const files = evt.target.files;
    for (let i = 0; i < files.length; i++) {
      const file = files[i];
      if (files && file) {
        const reader: FileReader = new FileReader();
        reader.onload = x => {
          const base64 = reader['result'] as string;
          if (!this.ProductData.images) {
            this.ProductData['images'] = [];
          }
          this.ProductData.images.push({
            base64: base64.split(',')[1],
            alt: 'alaunna image',
            description: 'alaunna image',
            action: 'new'
          });
        };
        reader.readAsDataURL(file);
      }
    }
  }
  onEditFileChange(evt) {
    const files = evt.target.files;
    const file = files[0];
    if (files && file) {
      const reader: FileReader = new FileReader();
      reader.onload = x => {
        const base64 = reader['result'] as string;
        this.ProductData['images'][this.currentImageId]['base64'] = base64.split(',')[1];
        this.ProductData['images'][this.currentImageId]['action'] = 'edited';
      };
      reader.readAsDataURL(file);
    }
  }
  onDeleteImageClicked(i) {
    if (this.ProductData['images'][i].action == 'new') {
      this.ProductData['images'].splice(i, 1);
    } else {
      this.ProductData['images'][i].action = 'deleted';
    }
  }
  onSubCategoryChange() {
    // console.log('changes');
    // console.log(this.subsubcategories);
    this.fitlerdSubsubCat = this.subsubcategories.filter(x => {
      return x.subCategory_id == this.ProductData['subCategory_id'];
    });
    this.updateInputs();
  }
  loadAllProductSubCategory() {
    this.productsController.getAllProductSubCategory(false).subscribe(
      data => {
        if (data['status'] === 'success') {
          this.filterdsubCat = data['data']['rows'].filter(x => {
            return x.productcategory_id === this.currentMarketData['marketcategory_id'];
          });
          SharedStorage.product_subcategories = data['data']['rows'];
        } else {
          this.snotify.error('هناك مشكله برجاء التحقق من الاتصال بالانترنت واعاده الاتصال');
        }
      },
      err => {
        this.snotify.error('هناك مشكله برجاء التحقق من الاتصال بالانترنت واعاده الاتصال');
      }
    );
  }
  loadAllProductSubSubCategory() {
    this.productsController.getAllProductSubSubCategory(false).subscribe(data => {
      if (data['status'] === 'success') {
        this.subsubcategories = data['data']['rows'];
        this.fitlerdSubsubCat = data['data']['rows'];
        SharedStorage.product_subsubcategories = data['data']['rows'];
      } else {
        this.snotify.error('هناك مشكله برجاء التحقق من الاتصال بالانترنت واعاده الاتصال');
      }
    },
      err => {
        this.snotify.error('هناك مشكله برجاء التحقق من الاتصال بالانترنت واعاده الاتصال');
      }
    );
  }
  updateInputs() {
    let curentSubcategory;
    if (this.filterdsubCat) {
      curentSubcategory = this.filterdsubCat.filter(x => {
        return x.id == this.ProductData['subCategory_id'];
      })[0];
    }
    let inputs = [];
    if (curentSubcategory && curentSubcategory.inputs) {
      inputs = inputs.concat(JSON.parse(curentSubcategory.inputs));
    }
    // console.log(curentSubcategory);
    this.inputs = inputs;
  }
  onEditClicked() {
    if (this.ImagePreProccessing()) {
      swal('خطأ', 'برجاء قم برفع صور ذات جوده جيده علي ان تكون ابعاد الصوره 220 X 220', 'error');
      return;
    }
    this.productsController.editProduct(this.ProductData).subscribe(data => {
      if (data['status'] == 'success') {
        swal('تم', 'تم تعديل المنتج بنجاح', 'success');
        this.router.navigate(['/markets/details/' + this.currentMarketData.id + '_' + this.currentMarketData.name]);
      } else {
        swal('خطأ', 'برجاء كتابه البيانات بشكل صحيح , وكتابه العناصر المطلوبه واختيار صور للمنتج في حدود الـ 700 X 700', 'error');
      }
    }, err => {
      swal('خطأ', 'برجاء كتابه البيانات بشكل صحيح , وكتابه العناصر المطلوبه واختيار صور للمنتج في حدود الـ 220 X 220', 'error');
    })
  }
  ImagePreProccessing() {
    let HaveError = false;
    for (let i = 0; i < this.ProductData.images.length; i++) {
      if (this.ProductData.images[i].action == 'deleted' || !this.ProductData.images[i].action ) {
        continue;
      }
      let ImageEl = document.getElementById(i + 'image');
      ImageEl.classList.remove('error');
      var realSize = this.realImgDimension(ImageEl);
      if (realSize.naturalHeight != 700 || realSize.naturalWidth != 700) {
        ImageEl.classList.add('error');
        HaveError = true;
      }
    }
    return HaveError;
  }
  realImgDimension(img) {
    var i = new Image();
    i.src = img.src;
    return {
      naturalWidth: i.width,
      naturalHeight: i.height
    };
  }

}
