import { ProductsService } from "./../services/products.service";
import { Injectable } from "@angular/core";
import { Router, ActivatedRouteSnapshot ,Resolve } from "@angular/router";
import { SharedStorage } from "../storageShared";
import swal from "sweetalert2";

@Injectable({
  providedIn: "root"
})
export class ProductDetailsResolveService  implements Resolve<any> {
  UserData = SharedStorage.current_user_data;
  constructor(
    private productController: ProductsService,
    private router: Router
  ) {}

  resolve(route: ActivatedRouteSnapshot): Promise<any> | boolean {
    let id = route.params["id"];
    if (id.indexOf("_")) {
      id = route.params["id"].split("_")[0];
    }
    return new Promise((resolve, reject) => {
      this.productController.getProductData(id).subscribe(
        data => {
          console.log(data);
          if (data["status"] == "success") {

            resolve(data["data"]);
          } else {
            reject(false);
          }
        },
        err => {
          reject(false);
        }
      );
    });
  }
}
