import { Injectable } from '@angular/core';
import { sharedClass } from '../sharedattrubite';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class ChatService {

  constructor(private http: HttpClient) { }
  LoadAllRooms(UserData) {
    console.log(UserData);
    let URL = `${sharedClass.BASE_URL}/room?market_id=${UserData.market_id}&offset=${UserData.offset}&user_id=${UserData.user_id}&search_word=${UserData.search_word}`;
    return this.http.get(URL);
  }

  LoadAllMessages(Data) {
    console.log(Data);
    let URL = `${sharedClass.BASE_URL}/messages?offset=${Data.offset}&user_id=${Data.user_id}&market_id=${Data.market_id}`;
    return this.http.get(URL);
  }

}
