import { sharedClass } from './../../../shared/sharedattrubite';
import { ProductsService } from './../../../shared/services/products.service';
import { BroadcasterService } from 'ng-broadcaster';
import { SharedStorage } from './../../../shared/storageShared';
import { Component, OnInit } from '@angular/core';

import { BsModalRef } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-market-product',
  templateUrl: './market-product.component.html',
  styleUrls: ['./market-product.component.css']
})
export class MarketProductComponent implements OnInit {

  throttle = 300;
  scrollDistance = 1;
  scrollUpDistance = 2;
  IsLoading = false;
  offset = 0;
  MarketProducts = [] as any;
  SelectedProducts;
  ImageBase = sharedClass.IMAGE_BASE_URL;
  MarketData = SharedStorage.current_user_data['markets'][0];
  WholeSelectedProducts = [];
  constructor(public productsController: ProductsService,
    public bsModalRef: BsModalRef,
    private broadCaster: BroadcasterService) { }

  ngOnInit() {
    if (this.MarketData) {
      this.getProducts();
    }
  }
  getProducts() {
    this.productsController.getProducts(false, false, false, this.MarketData['id'], 'active', this.offset , '').subscribe(data => {
      // console.log(data);
      if (data['status'] === 'success') {
        if (this.offset === 0) {
          this.MarketProducts = data['data']['rows'];
        } else {
          this.MarketProducts = this.MarketProducts.concat(data['data']['rows']);
        }
      }
    }, err => {

    });
  }
  onInputCheckedChange() {
    this.SelectedProducts = [];
    this.WholeSelectedProducts = [];
    for (const key in this.MarketProducts) {
      if (this.MarketProducts.hasOwnProperty(key)) {
        const element = this.MarketProducts[key];
        if (element.checked) {
          this.SelectedProducts.push(element.id);
          this.WholeSelectedProducts.push(element);
        }
      }

    }
  }
  onScroll() {
    if (!this.IsLoading) {
      this.offset++;
      this.getProducts();
    }
  }
  onSaveClicked() {
    if (this.SelectedProducts.length === 0) {
      alert('يجب عليك اضافه المنتجات التي تريد اضافتها لهذا العرض');
      return;
    }
    SharedStorage.SelectedProducts = this.SelectedProducts;
    SharedStorage.WholeSelectedProducts = this.WholeSelectedProducts;
    this.bsModalRef.hide();
  }

}
