import { SharedStorage } from './../../../shared/storageShared';
import { ProductsService } from './../../../shared/services/products.service';

import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';


@Component({
  selector: 'app-rate-product',
  templateUrl: './rate-product.component.html',
  styleUrls: ['./rate-product.component.css']
})
export class RateProductComponent implements OnInit {

  max = 5;
  isReadonly = false;

  overStar: number;
  percent: number;
  RateValue = 0;
  ProductData = {} as any;

  RateData = { value: 0 } as any;
  constructor(public bsModalRef: BsModalRef, private productController: ProductsService) { }

  ngOnInit() {
  }
  hoveringOver(value: number): void {
    this.overStar = value;
    this.percent = (value / this.max) * 100;
  }
  OnRateClicked() {
    this.RateData['product_id'] = this.ProductData['id'];
    this.RateData['user_id'] = SharedStorage.current_user_data['id'];
    this.productController.createNewrate(this.RateData).subscribe(data => {
      if (data['status'] === 'success') {
        this.bsModalRef.hide();
        alert('تم تقيم المنتج بنجاح');
      } else {
        if (data['error']['action'] === 'rate-before') {
          alert('تم تقيم المنتج من قبل');
        }
      }
    }, err => {

    });
  }
}
