import {
  Component,
  OnInit,
  NgZone,
  ApplicationRef,
} from "@angular/core";

import { SnotifyService } from "ng-snotify";

import { ActivatedRoute } from "@angular/router";
import { sharedClass } from "../../../core/shared/sharedattrubite";
import { HelperToolsService } from "../../../core/shared/services/helper-tools.service";
import { ChatService } from "../../../core/shared/services/chat.service";
import { AuthenticationService } from "../../../core/shared/services/authentication.service";
import { SharedStorage } from "../../../core/shared/storageShared";


@Component({
  selector: "app-chat",
  templateUrl: "./chat.component.html",
  styleUrls: ["./chat.component.css"]
})
export class ChatComponent implements OnInit {
  rooms = [];
  UserData = {} as any;
  filterData = { offset: 0, user_id: '', market_id: '' };
  ImageBase = sharedClass.IMAGE_BASE_URL;

  messages = [];
  currentRoom;
  message = '';

  constructor(private helperTools: HelperToolsService,
    private chatController: ChatService,
    private snotify: SnotifyService,
    private route: ActivatedRoute,
    private auth: AuthenticationService, private zone: NgZone  , private app : ApplicationRef) {

      this.auth.io.socket.on("newMessage", data => {
        console.log(data);
        this.zone.run(() => {
          this.messages.push(data);
          // this.scrollto();
        });
        this.app.tick();
      });

  }
  ngOnInit() {
    this.UserData = SharedStorage.current_user_data;
    console.log(this.UserData);
    this.route.params.subscribe(params => {
      this.filterData['market_id'] = params.market_id || ''; // 112
      this.filterData['user_id'] = params['user_id'] || '';
      if (this.UserData['category'] == 'seller') {
        this.filterData['market_id'] = this.UserData['markets'][0].id;
        this.getAllRooms();
      } else {
        this.filterData['user_id'] = this.UserData.id;
        this.getAllRooms();
      }

      if (this.filterData.user_id && this.filterData.market_id) {
        this.getAllMessages();
      }
    })

  }
  getAllRooms() {
    console.log(this.filterData);
    var RoomData = {};
    if (this.UserData['category'] == 'customer'){
      RoomData['user_id'] = this.UserData['id'];
      RoomData['market_id'] = ''
    } else {
      RoomData['market_id'] = this.UserData['markets'][0].id;
      RoomData['user_id'] = '';
    }
    RoomData['search_word'] = this.filterData['search_word'] || '';
    RoomData['offset'] = 0;
    console.log(RoomData);
    this.chatController.LoadAllRooms(RoomData).subscribe(data => {
      if (data['status'] == 'success') {
        this.rooms = data['data']['rows'];
        console.log(this.rooms);
      } else {
        this.snotify.error('هناك مشكله برجاء التأكد من الاتصال بالانترنت واعاده المحاوله');
      }
    }, err => {
      this.snotify.error('هناك مشكله برجاء التأكد من الاتصال بالانترنت واعاده المحاوله');
    })
  }
  getAllMessages() {

    this.chatController.LoadAllMessages(this.filterData).subscribe(data => {
      if (data['status'] == 'success') {
        this.messages = data['data']['messages'];
        this.currentRoom = data['data']['room'];
        console.log(data);
      } else {

      }
    }, err => {

    })
  }
  onLoadMore() {
    this.filterData.offset++;
    this.getAllRooms();
  }
  onSelectedOneroom(room) {
    this.currentRoom = room;
    if (this.UserData['category'] == 'seller') {
      this.filterData['user_id'] = room['user_id']
    } else {
      this.filterData['market_id'] = room['market_id'];
    }
    this.getAllMessages();
  }
  onSendClicked() {
    console.log(this.message);
    if (!this.message) {
      return;
    }
    let sentObj = {
      text: this.message,
      type: "message",
      room_id: this.currentRoom['id']
    }
    if (this.UserData.category == 'seller') {
      sentObj['from_market_id'] = this.UserData['markets'][0]['id'];
    } else {
      sentObj['from_user_id'] = this.UserData['id'];
    }
    this.auth.io.socket.post("/message/send", sentObj,
      (body, JWR) => {
        this.zone.run(() => {
          if (body["status"] == "success") {
            this.message = "";
            // this.scrollto();
            this.messages.push(body["data"]);
          }
        });
      }
    );
  }
  onGetImage() {

  }
  onFileChanges(evt) {
    const files = evt.target.files;
    for (let i = 0; i < files.length; i++) {
      const file = files[i];
      if (files && file) {
        const reader: FileReader = new FileReader();
        reader.onload = x => {
          const base64 = reader['result'] as string;
          // if (!this.ProductData.images) {
          //   this.ProductData['images'] = [];
          // }
          // this.ProductData.images.push({
          //   base64: base64.split(',')[1],
          //   alt: 'alaunna image',
          //   description: 'alaunna image',
          // });

          let sentObj = {};
          if (this.UserData.category == 'seller') {
            sentObj['from_market_id'] = this.UserData['markets'][0]['id'];
          } else {
            sentObj['from_user_id'] = this.UserData['id'];
          }
          sentObj['type'] = 'image';
          sentObj['room_id'] = this.currentRoom.id;
          sentObj['image'] = {
            base64: base64.split(',')[1],
            alt: 'alaunna image',
            description: 'alaunna image',
          };
          this.auth.io.socket.post("/message/send", sentObj,
            (body, JWR) => {
              this.zone.run(() => {
                if (body["status"] == "success") {
                  this.message = "";
                  // this.scrollto();
                  this.messages.push(body["data"]);
                }
              });
            }
          );

        };
        reader.readAsDataURL(file);
      }
    }
  }
}
